// Paaliname axios import ir naudojame fetch API
// import axios from 'axios'; - io nebereikia

// Funkcija, kuri analizuoja uduot ir silo subtask sprendimo bdus
export const getSubtaskSuggestions = async (task, subtaskIndex) => {
  try {
    // Jei nra subtask, griname bendr patarim
    if (!task.subtasks || task.subtasks.length === 0) {
      return {
        suggestion: "iai uduoiai nra nustatyt ingsni. Galite j tiesiog paymti kaip atlikt.",
        resources: []
      };
    }

    // Gauname subtask, kuriam reikia pagalbos
    const subtask = task.subtasks[subtaskIndex];
    const subtaskText = typeof subtask === 'string' ? subtask : subtask.text;
    
    // Sukuriame uklaus AI modeliui
    const prompt = `Padk man atlikti  uduoties ingsn: "${subtaskText}". 
    Uduoties kontekstas: "${task.text}". 
    Praau pateikti:
    1. Trump paaikinim, kaip atlikti  ingsn (ne daugiau 3 sakini)
    2. Bent 2 konkreius veiksmus, kuriuos galiu atlikti
    3. Jei manoma, nuorod  nauding altin`;

    // Naudojame fetch API vietoj axios
    const response = await fetch(`https://text.pollinations.ai/${encodeURIComponent(prompt)}?model=openai&private=true`);
    
    if (!response.ok) {
      throw new Error('API atsakymo klaida: ' + response.status);
    }
    
    // Gauname atsakym kaip tekst
    const aiResponse = await response.text();
    
    // Paprastas bdas itraukti resursus i atsakymo - iekome URL
    const urlRegex = /(https?:\/\/[^\s]+)/g;
    const foundUrls = aiResponse.match(urlRegex) || [];
    
    // Griname struktrizuot atsakym
    return {
      suggestion: aiResponse.replace(urlRegex, '').trim(),
      resources: foundUrls
    };
  } catch (error) {
    console.error('Klaida gaunant AI pasilymus:', error);
    return {
      suggestion: "Nepavyko gauti pasilym. Bandykite vliau arba atlikite ingsn savarankikai.",
      resources: []
    };
  }
};

// Funkcija, kuri analizuoja uduot ir silo, kiek laiko skirti kiekvienam subtaskui
export const getTimeEstimates = async (task) => {
  try {
    if (!task.subtasks || task.subtasks.length === 0) {
      return {
        totalEstimate: 15, // Numatytasis laikas minutmis
        subtaskEstimates: [15]
      };
    }

    // Gauname visus subtaskus
    const subtaskTexts = task.subtasks.map(st => 
      typeof st === 'string' ? st : st.text
    );
    
    // Sukuriame uklaus AI modeliui
    const prompt = `Analizuok i uduot: "${task.text}" su iais ingsniais:
    ${subtaskTexts.map((st, i) => `${i+1}. ${st}`).join('\n')}
    
    Kiek minui utrukt kiekvienas ingsnis? Pateik tik skaiius, po vien eilutje.`;

    // Naudojame fetch API vietoj axios
    const response = await fetch(`https://text.pollinations.ai/${encodeURIComponent(prompt)}?model=openai&private=true`);
    
    if (!response.ok) {
      throw new Error('API atsakymo klaida: ' + response.status);
    }
    
    // Gauname atsakym kaip tekst
    const aiResponse = await response.text();
    
    // Apdorojame atsakym - itraukiame skaiius
    const numberRegex = /\d+/g;
    const numbers = aiResponse.match(numberRegex) || [];
    
    // Konvertuojame  skaiius ir utikriname, kad turime pakankamai veri
    let subtaskEstimates = numbers.map(n => parseInt(n, 10));
    
    // Jei negavome pakankamai veri, upildome numatytaisiais (10 min)
    while (subtaskEstimates.length < subtaskTexts.length) {
      subtaskEstimates.push(10);
    }
    
    // Jei gavome per daug, patrumpinkime
    subtaskEstimates = subtaskEstimates.slice(0, subtaskTexts.length);
    
    // Apskaiiuojame bendr laik
    const totalEstimate = subtaskEstimates.reduce((sum, time) => sum + time, 0);
    
    return {
      totalEstimate,
      subtaskEstimates
    };
  } catch (error) {
    console.error('Klaida gaunant laiko verius:', error);
    
    // Griname numatytuosius verius
    const defaultEstimate = 10;
    const subtaskCount = task.subtasks ? task.subtasks.length : 1;
    
    return {
      totalEstimate: defaultEstimate * subtaskCount,
      subtaskEstimates: Array(subtaskCount).fill(defaultEstimate)
    };
  }
};

// Funkcija, kuri silo, kaip pagerinti uduoties atlikim
export const getTaskCompletionTips = async (task) => {
  try {
    const prompt = `Analizuok i uduot: "${task.text}".
    
    Pateik 3 trumpus patarimus, kaip efektyviai atlikti i uduot. 
    Kiekvienas patarimas turi bti ne ilgesnis nei 15 odi.`;

    // Naudojame fetch API vietoj axios
    const response = await fetch(`https://text.pollinations.ai/${encodeURIComponent(prompt)}?model=openai&private=true`);
    
    if (!response.ok) {
      throw new Error('API atsakymo klaida: ' + response.status);
    }
    
    // Gauname atsakym kaip tekst
    const aiResponse = await response.text();
    
    // Apdorojame atsakym - iskaidome  atskirus patarimus
    let tips = aiResponse.split(/\d+\.\s+|\n+/).filter(tip => tip.trim().length > 0);
    
    // Jei negavome bent vieno patarimo, griname numatytj
    if (tips.length === 0) {
      tips = ["Suskirstyk uduot  maesnius ingsnius ir atlik juos po vien."];
    }
    
    return tips;
  } catch (error) {
    console.error('Klaida gaunant uduoties patarimus:', error);
    return [
      "Suskirstyk uduot  maesnius ingsnius ir atlik juos po vien.",
      "Nustatyk konkret laik uduoiai atlikti ir laikykis jo.",
      "Paalink trukdius ir susitelk  uduot."
    ];
  }
};

// Funkcija, kuri silo, kaip sprsti uduoties atlikimo problemas
export const getTroubleshootingAdvice = async (task, problem) => {
  try {
    const prompt = `Man kyla problem atliekant i uduot: "${task.text}".
    
    Konkreti problema: "${problem}".
    
    Pateik 2-3 konkreius sprendimo bdus.`;

    // Naudojame fetch API vietoj axios
    const response = await fetch(`https://text.pollinations.ai/${encodeURIComponent(prompt)}?model=openai&private=true`);
    
    if (!response.ok) {
      throw new Error('API atsakymo klaida: ' + response.status);
    }
    
    // Gauname atsakym kaip tekst
    const aiResponse = await response.text();
    
    return aiResponse;
  } catch (error) {
    console.error('Klaida gaunant problem sprendimo patarimus:', error);
    return "Bandykite uduot suskaidyti  maesnius ingsnius. Jei ustrigote, padarykite pertrauk ir grkite prie uduoties vliau su vieiu poiriu.";
  }
};

/**
 * Funkcija teksto generavimui naudojant Pollinations API
 * @param {string} prompt Vartotojo vestas tekstas
 * @param {AbortSignal} signal AbortSignal objektas uklausos nutraukimui
 * @returns {Promise<string>} Sugeneruotas tekstas
 */
export const generateTips = async (prompt, signal) => {
  // Pagerintas promptas, kad AI atsakyt kaip produktyvumo ekspertas
  const enhancedPrompt = `Produktyvumo ekspertas: ${prompt}`;
  
  try {
    const response = await fetch('https://text.pollinations.ai/', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        messages: [{ role: 'user', content: enhancedPrompt }],
        model: 'openai',
        private: true,
        system: "Tu esi produktyvumo ekspertas, kuris duoda konkreius, praktikus, detalius atsakymus apie tai, kaip geriau atlikti uduotis ir efektyviau valdyti laik. Tavo atsakymai yra trumpi, aiks ir nedviprasmiki."
      }),
      signal
    });
    
    if (!response.ok) {
      throw new Error(`API klaida: ${response.status}`);
    }
    
    const data = await response.json();
    if (data && data.message && data.message.content) {
      return data.message.content;
    } else {
      return "Atsipraau, nepavyko gauti atsakymo. Praome bandyti vliau.";
    }
  } catch (error) {
    console.error("Klaida gaunant AI atsakym:", error);
    if (error.name === 'AbortError') {
      throw error; // Perduodame AbortError aukiau, kad galtume tinkamai apdoroti
    }
    return "Atsipraau, vyko klaida. Praome bandyti vliau.";
  }
};

// Eksportuojame visas funkcijas
export default {
  getSubtaskSuggestions,
  getTimeEstimates,
  getTaskCompletionTips,
  getTroubleshootingAdvice,
  generateTips
}; 